	import ruiDatePicker from '../../../components/rattenking-dtpicker/rattenking-dtpicker.vue'
	import tTable from '@/components/t-table/t-table.vue'
	import tTh from '@/components/t-table/t-th.vue'
	import tTr from '@/components/t-table/t-tr.vue'
	import tTd from '@/components/t-table/t-td.vue'
	var timer_s = getNowFormatDate()
	var timer_e = getNowFormatDate()
	/* timer_s = timer_s.substring(0,7)+"-01"; */
	
	 function getNowFormatDate() {
		var date = new Date();
		var seperator1 = "-";
		var seperator2 = ":";
		var month = date.getMonth() + 1;
		var strDate = date.getDate();
		if (month >= 1 && month <= 9) {
			month = "0" + month;
		}
		if (strDate >= 0 && strDate <= 9) {
			strDate = "0" + strDate;
		}
		var currentdate = date.getFullYear() + seperator1 + month + seperator1 +
			strDate;
		return currentdate;
	} 
	export default {
		components: {
			ruiDatePicker,
			tTable,
			tTh,
			tTr,
			tTd
		},
		data() {
			return {
				size:'',
				data:[],
				max: 0,
				loadMoreText: "",
				showLoadMore: true,
				time:'',
				tableList:[],
				dates:timer_s,
				datee:timer_e,
				huohao:'',
				chuanghao:'',
				color:'',
				chima:'',
				userid:'',
				uid:'',
				worktype:'',
				bs:'',
				sl:'',
				dk:''
			}
		},
		onBackPress(options) {
			console.log("###"+JSON.stringify(options));
			if (options.from === 'backButton') {
				uni.switchTab({
					url: '/pages/admin/admin'
				});
				return true;
			}
			return false;
		},
		onUnload() {
			this.max = 0,
			this.data = [],
			this.loadMoreText = "加载更多",
			this.showLoadMore = false;
			console.log('onUnload事件');
		},
		onReachBottom() {
			console.log("onReachBottom");
			console.log('max：'+this.max );
			if (this.max >= this.size) {
				this.loadMoreText = "没有更多数据了!"
				return;
			}
			this.showLoadMore = true;    //设置下拉提示的标记
			setTimeout(() => {
				this.setDate();
			}, 500);
		}, 
		onLoad(r) {
			 console.log("diyici:"+JSON.stringify(r)); 
			 let temp = JSON.stringify(r);
			 if(temp.indexOf('.html') < 0 && JSON.stringify(r) !='{}'){  
                    console.log(r.huohao);
					this.huohao = r.huohao;
					this.chuanghao = r.chuanghao;
					this.color = r.color
					this.chima = r.chima
					this.userid = r.userid
					this.uid = r.uid
					this.worktype = r.worktype
					this.time = r.time
					let temp = r.time.split(',');
					this.dates = temp[0];
					this.datee = temp[1];
			}
			uni.showLoading({
				title: '加载中....',
				mask:true
			})
			this.dk = this.websiteUrl
			uni.getStorage({
				key:'fwqsj',success: res => {
					this.dk = res.data
					this.initData()
				}
			})	
		},
		methods: {
			initData(){
				setTimeout(() =>{
					uni.request({
						url: `https://${this.dk}/getSclzdHz.aspx`,
						method: 'GET',
						data: {
							dates:this.dates,
							datee:this.datee,
							huohao:this.huohao,
							chuanghao:this.chuanghao,
							color:this.color,
							chima:this.chima,
							userid:this.userid,
							uid:this.uid,
							worktype:this.worktype
						},
						success: res => {
							setTimeout(function () {
								uni.hideLoading();
							}, 3000)
							if(res.data.length>8000){
								uni.showModal({
									title:'友情提示',
									content:'您的数据量过大，请到电脑端查询',
									showCancel:false,
									confirmText:'关闭'
								})
								return
							}
						   this.tableList = res.data
						   this.size = this.tableList.length
						   let temp = res.data
						   let bs =0;
						   let sl =0
						   for (let i = 0;i <temp.length;i++) {
						   	bs = bs+temp[i].bs
						   	sl = sl+temp[i].sl
						   }
						   this.bs = bs
						   this.sl= sl;
							
							//一下为下拉使用
							
							this.max = 0;
							this.data = [];
							let data = [];
							this.max += (this.size<=20?this.size:20);
							let temp1 = this.tableList.splice(0,20);
							for(var i = 1;i <= temp1.length;i++){
								data.push(temp1[i-1]);
							}
							this.data = this.data.concat(data);
							uni.stopPullDownRefresh();				
						}
					})
				},2000);
			},
			setDate() {
				let data = [];
				this.max += (Math.floor(this.tableList.length/20)>0?20:this.tableList.length);
				let temp1 = this.tableList.splice(0,20);
				for(var i = 1;i <= temp1.length;i++){
					data.push(temp1[i-1]);
				}
				this.data = this.data.concat(data);
			},
			bindChange1(value){
				this.dates = value;
			},
			bindChange2(value){
				this.datee = value;
			},
			onpeDetailed(){
				uni.showLoading({
					title: '加载中....',
					mask:true
				})
				this.initData()
			},
			onpeIfon(){
				var date = new Date();
				var month = date.getMonth()+1;
				uni.redirectTo({
					url:'../../info/info?flag=1&month='+month
				})
			}
		},
		
	}
